.input-text {
  height: 45px;
  border: 1px solid rgba(207, 207, 207, 0.5);
  padding: 3%; }
  .input-text:hover {
    border: 1px solid #cdcdcd; }
  .input-text:active + .input-text__icon {
    fill: #68b738; }
  .input-text:invalid + .input-text__icon {
    fill: #ff8888; }
  .input-text:active, .input-text:focus {
    border: 2px solid black;
    outline: none; }
  .input-text:invalid {
    border: 2px solid #ff8888; }

.input-text__icon {
  position: absolute;
  right: 10px;
  top: 25%; }

@media (min-width: 768px) {
  .input-text {
    height: 51px; }
  .input-text__icon {
    left: 95%;
    top: 25%; } }

@media (min-width: 1440px) {
  .input-text {
    height: 51px; } }

.radio + label::before {
  content: "";
  width: 30px;
  height: 30px;
  background-color: white;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
  border: 1px solid rgba(207, 207, 207, 0.5);
  margin-right: 15px;
  cursor: pointer; }

.radio + label:hover::before {
  border: 1px solid #cdcdcd; }

.radio:disabled + label::before {
  border: 1px solid #cfcfcf; }

.radio:disabled + label {
  opacity: 0.3; }

.radio:checked + label::before {
  content: "";
  background: radial-gradient(ellipse at center, #68b738 0%, #68b738 30%, white 30%, white 100%);
  vertical-align: middle;
  border-radius: 50%;
  border: 1px solid rgba(207, 207, 207, 0.5); }

.radio:checked + label:hover::before {
  border: 1px solid #cdcdcd; }

.radio:checked:disabled + label:hover::before {
  border: 1px solid #cfcfcf; }

.radio:checked:disabled + label:hover {
  opacity: 0.3; }

@media (min-width: 768px) {
  .radio + label::before {
    width: 40px;
    height: 40px; } }

.checkbox + label::before {
  content: "";
  width: 30px;
  height: 30px;
  background-color: white;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid rgba(207, 207, 207, 0.5);
  margin-right: 15px;
  cursor: pointer; }

.checkbox + label:hover::before {
